import React, { Component } from "react";
import { Container, Button } from "react-bootstrap";

import "./CatalogCard.css";
import { Link } from "react-router-dom";

export default class CatalogCard extends Component {
  render() {
    return (
      <div className="bg-white py-3 mx-1 font_pfespl">
        <h4 className="item-price text-red mb-3">{this.props.item.price}$</h4>
        <img
          src={this.props.item.testdriveImgPath}
          max-width="220"
          className="img-fluid mt-3 item-img mx-auto"
        />
        <h5 className="item-model my-3">{this.props.item.model}</h5>

        <ul className="text-left" style={{listStyle: 'none'}}>
          <li>: <span className="text-red">{this.props.item.bodyType}</span></li>
          <li>: <span className="text-red">{this.props.item.gear}</span></li>
          <li>  100 /: <span className="text-red">{this.props.item.start} .</span></li>
          <li>. : <span className="text-red">{this.props.item.maxSpeed} /</span></li>
          <li>: <span className="text-red">{this.props.item.power}</span></li>
          <li> : <span className="text-red">{this.props.item.maxCharge} </span></li>
          <li>: <span className="text-red">{this.props.item.distance}  </span></li>
        </ul>
        
        <Link to={`/catalog/${this.props.item._id}`} onClick={()=>{this.props.selected(this.props.item._id)}} className="btn btn-danger item-btn my-3"></Link>
        
      </div>
    );
  }
}
