import React, { Component } from "react";
import Slider from "react-slick";
import BFCarouselItem from "./BFCarouselItem";
import car1 from "../assets/bfs-1.png";
import car2 from "../assets/bfs-2.png";
import car3 from "../assets/bfs-3.png";
import car4 from "../assets/bfs-4.png";
import car5 from "../assets/bfs-5.png";
import car6 from "../assets/bfs-6.png";
import { Row, Col } from "react-bootstrap";

export default class BFCarousel extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      autoplay: false,
      autoplaySpeed: 9000,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
    };
    const slides = [
      {
        id: 1,
        img: car1,
        header: "     ",
        spans: [
          "     , ,       .",
          "             .",
          "        .     ,    /.",
        ],
      },
      {
        id: 2,
        img: car2,
        header: "     ",
        spans: [
          "       ,   ,                 .",
          "      .",
        ],
      },
      {
        id: 3,
        img: car3,
        header: "  -",
        spans: [
          "         ,        .",
          "       , , ,  ,     .",
        ],
      },
      {
        id: 4,
        img: car4,
        header: "     !",
        spans: [
          "     .               .",
          "  2022           .                 .",
        ],
      },
      {
        id: 5,
        img: car5,
        header: "   !",
        spans: [
          "           .              .",
          '     :  , , ,   , " ",    .',
        ],
      },
      {
        id: 6,
        img: car6,
        header: "    - !",
        spans: [
          "     .",
          "   ,    .",
          "  , ,             .",
        ],
      },
    ];

    return (
      <div className="mt-5 pb-5">
        <Slider {...settings} className="">
          <div className="container-fluid">
            <Row className="my-1">
              <Col className="col-12 col-md-6 bg-white p-0">
                <div className="bf-text">
                  <h5 className="text-uppercase py-4 text-60">
                    {slides[0].header}
                  </h5>
                  <ul className="font_pfespl">
                    {slides[0].spans.map((span) => (
                      <li
                        key={span.indexOf()}
                        className="mb-3 text-left text-red"
                      >
                        <span className="text-75">{span}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* <div className="white1"></div> */}
                <div className="red"></div>
              </Col>
              <Col className="col-12 col-md-6 bg-white p-0">
                <img src={slides[0].img} className="pic" />
              </Col>
            </Row>
          </div>
          <div className="container-fluid">
            <Row className="my-1">
              <Col className="col-12 col-md-6 bg-white p-0">
                <div className="bf-text">
                  <h5 className="text-uppercase py-4 text-60">
                    {slides[1].header}
                  </h5>
                  <ul className="font_pfespl">
                    {slides[1].spans.map((span) => (
                      <li
                        key={span.indexOf()}
                        className="mb-3 text-left text-red"
                      >
                        <span className="text-75">{span}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* <div className="white2"></div> */}
                <div className="red"></div>
              </Col>
              <Col className="col-12 col-md-6 bg-white p-0">
                <img src={slides[1].img} className="pic" />
              </Col>
            </Row>
          </div>
          <div className="container-fluid">
            <Row className="my-1">
              <Col className="col-12 col-md-6 bg-white p-0">
                <div className="bf-text">
                  <h5 className="text-uppercase py-4 text-60">
                    {slides[2].header}
                  </h5>
                  <ul className="font_pfespl">
                    {slides[2].spans.map((span) => (
                      <li
                        key={span.indexOf()}
                        className="mb-3 text-left text-red"
                      >
                        <span className="text-75">{span}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* <div className="white3"></div> */}
                <div className="red"></div>
              </Col>
              <Col className="col-12 col-md-6 bg-white p-0">
                <img src={slides[2].img} className="pic" />
              </Col>
            </Row>
          </div>
          <div className="container-fluid">
            <Row className="my-1">
              <Col className="col-12 col-md-6 bg-white p-0">
                <div className="bf-text">
                  <h5 className="text-uppercase py-4 text-60">
                    {slides[3].header}
                  </h5>
                  <ul className="font_pfespl">
                    {slides[3].spans.map((span) => (
                      <li
                        key={span.indexOf()}
                        className="mb-3 text-left text-red"
                      >
                        <span className="text-75">{span}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* <div className="white4"></div> */}
                <div className="red"></div>
              </Col>
              <Col className="col-12 col-md-6 bg-white p-0">
                <img src={slides[3].img} className="pic" />
              </Col>
            </Row>
          </div>
          <div className="container-fluid">
            <Row className="my-1">
              <Col className="col-12 col-md-6 bg-white p-0">
                <div className="bf-text">
                  <h5 className="text-uppercase py-4 text-60">
                    {slides[4].header}
                  </h5>
                  <ul className="font_pfespl">
                    {slides[4].spans.map((span) => (
                      <li
                        key={span.indexOf()}
                        className="mb-3 text-left text-red"
                      >
                        <span className="text-75">{span}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* <div className="white5"></div> */}
                <div className="red"></div>
              </Col>
              <Col className="col-12 col-md-6 bg-white p-0">
                <img src={slides[4].img} className="pic" />
              </Col>
            </Row>
          </div>
          <div className="container-fluid">
            <Row className="my-1">
              <Col className="col-12 col-md-6 bg-white p-0">
                <div className="bf-text">
                  <h5 className="text-uppercase py-4 text-60">
                    {slides[5].header}
                  </h5>
                  <ul className="font_pfespl">
                    {slides[5].spans.map((span) => (
                      <li
                        key={span.indexOf()}
                        className="mb-3 text-left text-red"
                      >
                        <span className="text-75">{span}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* <div className="white6"></div> */}
                <div className="red"></div>
              </Col>
              <Col className="col-12 col-md-6 bg-white p-0">
                <img src={slides[5].img} className="pic" />
              </Col>
            </Row>
          </div>
        </Slider>
      </div>
    );
  }
}
